.spinner {
    display: inline-block;
    margin: 7px 15px;
}

.spinner svg {
    -webkit-animation: spinner-rotation 1.4s linear infinite;
    -o-animation: spinner-rotation 1.4s linear infinite;
    animation: spinner-rotation 1.4s linear infinite;
    stroke: #4285f4;
}

.spinner svg .circle {
    stroke-dasharray: 187;
    stroke-dashoffset: 0;
    -webkit-transform-origin: center;
    -ms-transform-origin: center;
    -o-transform-origin: center;
    transform-origin: center;
    -webkit-animation: spinner-turn 1.4s ease-in-out infinite;
    -o-animation: spinner-turn 1.4s ease-in-out infinite;
    animation: spinner-turn 1.4s ease-in-out infinite;
}

.verses-wrapper .verse-book {
    margin-bottom: 5px;
    font-size: 25px;
}

.verses-wrapper .verse-chapter {
    margin-top: 10px;
    font-size: 22px;
}

.verses-wrapper .verse {
    margin-bottom: 5px;
    cursor: pointer;
}

.verses-wrapper .verse:hover {
    background-color: #eee;
}

.verses-wrapper .verse.selected {
    background-color: #dcdcdc;
}

#verse-options {
    position: absolute;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
    z-index: 1000;
    display: inline-block;
    float: left;
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: 14px;
    text-align: left;
    list-style: none;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0, 0, 0, .15);
    border-radius: 4px;
    -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
    box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
}


@-webkit-keyframes spinner-rotation {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(270deg);
        transform: rotate(270deg);
    }
}

@-o-keyframes spinner-rotation {
    0% {
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -o-transform: rotate(270deg);
        transform: rotate(270deg);
    }
}

@keyframes spinner-rotation {
    0% {
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(270deg);
        -o-transform: rotate(270deg);
        transform: rotate(270deg);
    }
}

@-webkit-keyframes spinner-turn {
    0% {
        stroke-dashoffset: 187;
    }
    50% {
        stroke-dashoffset: 46.75;
        -webkit-transform: rotate(135deg);
        transform: rotate(135deg);
    }
    100% {
        stroke-dashoffset: 187;
        -webkit-transform: rotate(450deg);
        transform: rotate(450deg);
    }
}

@-o-keyframes spinner-turn {
    0% {
        stroke-dashoffset: 187;
    }
    50% {
        stroke-dashoffset: 46.75;
        -o-transform: rotate(135deg);
        transform: rotate(135deg);
    }
    100% {
        stroke-dashoffset: 187;
        -o-transform: rotate(450deg);
        transform: rotate(450deg);
    }
}

@keyframes spinner-turn {
    0% {
        stroke-dashoffset: 187;
    }
    50% {
        stroke-dashoffset: 46.75;
        -webkit-transform: rotate(135deg);
        -o-transform: rotate(135deg);
        transform: rotate(135deg);
    }
    100% {
        stroke-dashoffset: 187;
        -webkit-transform: rotate(450deg);
        -o-transform: rotate(450deg);
        transform: rotate(450deg);
    }
}

